export interface iEnableRule {
    logic?: 'AND' | 'OR';
    conditions: {
        fieldKey: string;
        operator: 'eq' | 'neq' | 'gt' | 'lt' | 'isNotEmpty' | 'isEmpty' | 'matches' | 'isValid';
        value?: any;
    }[];
}

export interface iFormField {
    size: 1 | 2 | 3 | 4;
    key: string;
    label?: string;
    placeholder?: string;
    sequence: number;
    fieldType: eFormField;
    value: any;
    validators: any[];
    errors: string[];
    isPristine: boolean;
    options?: { [key: string]: any }[];
    optionLabel?: string;
    optionValue?: string;
    optionDescription?: string;
    optionSize?: 1 | 2 | 3 | 4;
    disabled?: boolean;
    hidden?: boolean;
    shouldEnable?: iEnableRule;
    shouldDisplay?: iEnableRule;
    acceptedFiles?: string[];
}

export enum eFormField {
    input = 'input',
    password = 'password',
    select = 'select',
    textarea = 'textarea',
    checkbox = 'checkbox',
    toggle = 'toggle',
    date = 'date',
    legend = 'legend',
    radio = 'radio',
    phone = 'phone',
    file = 'file',
    multifile = 'multifile',
    number = 'number',

    country = 'country',
    typeahead = 'typeahead',
}

